.admin-bar {

	.fixed-top,
	.sticky-top {
		top: 46px;

		@include media-breakpoint-up(md) {
			top: 32px;
		}
	}
}

.mw-300 {
    max-width: 300px;
  }
  
  .mw-400 {
    max-width: 400px;
  }
  
  .mw-500 {
    max-width: 500px;
  }
  
  .mw-600 {
    max-width: 600px;
  }
  
  .mw-700 {
    max-width: 700px;
  }
  
  .mw-800 {
    max-width: 800px;
  }
  
.mt-neg-200 {
    margin-top: -200px;
    position: relative;
    z-index: 1;
}

.pagination {
	margin-top: 2rem;
	justify-content: center;

	.page-link {
		border: 0;
		border-radius: 100px !important;
		margin: 2px;
	}
}

.comments-area {
	margin-top: $spacer * 3;
}

