.epicpower-menu-toggler {
  position: absolute;
  top: 28px;
  right: 20px;
  width: 50px;
  height: 24px;
  z-index: 100;
  transition: all 0.3s ease;
}
.epicpower-menu-toggler:hover {
  transform: scale(0.9);
}
.epicpower-menu-toggler .slot {
  background-color: #fff;
  height: 3px;
  width: 34px;
  display: block;
  transition: all 0.3s ease;
  position: absolute;
  left: 0;
  top: 0;
}
.epicpower-menu-toggler .slot.slot-1 {
  top: 0;
}
.epicpower-menu-toggler .slot.slot-2 {
  top: 10px;
}
.epicpower-menu-toggler .slot.slot-3 {
  top: 20px;
}
.epicpower-menu-toggler.cerrar .slot {
  box-shadow: none;
}
.epicpower-menu-toggler.cerrar .slot-2 {
  opacity: 0;
}
.epicpower-menu-toggler.cerrar .slot-1 {
  transform: rotate(45deg) translateY(0);
  top: 50%;
}
.epicpower-menu-toggler.cerrar .slot-3 {
  transform: rotate(-45deg) translateY(0);
  top: 50%;
}

/* Filtros */
.archive-filter {
